.App {
  width: 100vw;
  height: 100vh;
  background-color: lightblue;
}

.MainBody {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background-image: url("../img/space.jpg");
  background-repeat: repeat-x;
  background-color: rgb(2, 2, 2);

  animation: slide 90s linear infinite;

  @keyframes slide {
    from {
      background-position: 0 0;
    }

    to {
      background-position: -1120px 0;
    }
  }

  .topComponents {
    flex: 0 0 70%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .bottomComponents {
    overflow: hidden;
    flex: 0 0 30%;
    width: 100%;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 50% 0%;

      transition: top 15s ease-out;
      position: relative;
      top: 100%;

      &.moveIn {
        top: 0%;
      }
    }
  }

  #plane {
    position: absolute;
    width: auto;
    height: 15%;
    left: 65%;
    bottom: 15%;
    animation: MoveUpDown 10s 2s ease-in-out infinite, MoveLeftRight 17s 4s ease-in-out infinite;

    img {
      width: auto;
      height: 100%;
    }

    .particlesContainer {
      position: relative;
      height: 20%;
      width: 75%;
      bottom: 60%;
      right: 75%;
    }
  }

  @keyframes MoveUpDown {

    0%,
    100% {
      bottom: 15%
    }

    50% {
      bottom: 17%
    }
  }

  @keyframes MoveLeftRight {

    0%,
    100% {
      left: 65%
    }

    50% {
      left: 62%
    }
  }

}