.Fader {
    position: fixed;
    z-index: 10;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 1);
    transition: background 4s;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    &.fading {
        background-color: rgba(255, 255, 255, 1);
    }

    &.revealing {
        transition: background 4s;
        background-color: rgba(255, 255, 255, 0);
    }

    &.revealed {
        display: none;
    }

    .ticketImage {
        width: 75%;
        max-width: 620px;
        height: 75%;
        max-height: 496px;
        object-fit: contain;
        -webkit-filter: drop-shadow(40px 20px 54px rgba(255, 255, 255, 0.68));
        filter: drop-shadow(40px 20px 54px rgba(255, 255, 255, 0.68));
        cursor: pointer;

        opacity: 0;
        transition: opacity 2s ease-in-out;

        &.revealing {
            opacity: 1;
        }
    }
}